	module.exports = {"data":{"markdownRemark":{"html":"<h3>In-Product Notifications for Invites</h3>\n<p>The notifications corner now pings hackers about new invitations.</p>\n<h3>Invitations on the Program’s Profile</h3>\n<p>Hackers can also see their invitations on the program's profile page. This reminds hackers of their invitation when they go to look at the program.</p>\n<h3>Pending Invitations Page</h3>\n<p>The new Pending Invitations page enables hackers to view all of their pending invites in one place so that they can see all the invitations they need to take action on.</p>\n<h3>Rejection Questionnaire</h3>\n<p>When Hackers reject an invite, they are given the opportunity to fill out a questionnaire to provide HackerOne with feedback on why they decided to reject the program invitation. The questionnaire shows up directly after hackers reject the invitation.</p>\n<h3>Leave Program Button</h3>\n<p>The Leave Program button is updated to be on the sidebar of the program’s security page. Hackers that leave the program also also get an invitation to fill out the rejection questionnaire.</p>\n<h3>Priority Queue</h3>\n<p>Hackers that submit the rejection questionnaire are placed at the top of the queue for the next program invitation they qualify for.</p>\n<h3>Private Invite Notification</h3>\n<p>The notification to private invites is updated so that it doesn't look like a program member invite.</p>\n<h3>Response Efficiency Box</h3>\n<p>The Response Efficiency box is updated on the program security page to show that metrics are averages of the last 90 days.</p>\n<h3>Response Efficiency Indicator</h3>\n<p>There is now a response indicator in the Response Efficiency box of the program's security page to show how healthy a program is. The indicators are either green, orange, or red dots.</p>\n<h3>Bug Fixes</h3>\n<ul>\n<li>Hackers are no longer redirected to a deleted program after every login.</li>\n<li>Programs can mark reports as being ineligible for bounty even though a hacker account is disabled.</li>\n<li>The program health alerts are fixed so that you're not getting alerted when you have 0 reports failing SLAs.</li>\n<li>Old resolved reports are no longer marked as SLA Fail or SLA Miss.</li>\n</ul>","frontmatter":{"path":"/changelog/2018/february","title":"February 2018"}}},"pathContext":{}}

